import * as React from "react";
import { ReactComponent } from './reactcomponent';

export interface ISwitch {
    checked: boolean,
    onClick: any
}

export class CSwitch extends ReactComponent<ISwitch, ISwitch> {

    protected bindings(): void {}
    protected setInitialState(props: ISwitch): void {
        this.state = props;
    }

    render() {
        const { checked, onClick } = this.props;
        return (
            <div className={`bd-switch${checked ? ' bd-switch-checked' : ''}`} onClick={onClick}>
                <input type="checkbox" className="bd-switch-checkbox"/>
            </div>
        );
    }

}


